.header {
  box-sizing: border-box;
  background-image: url(../images/header/header-bg-mobile-bike.png);
  background-size: cover;

  @include on-tablet {
    background-image: url(../images/header/header-bg-tablet-bike.png);
    background-position: 70%;
  }

  @include on-desktop {
    background-image: url(../images/header/header-bg-desktop-bike.png);
    background-position: 65%;
  }


  &__container {
    margin: 0 auto;
    max-width: 578px;
    height: 100vh;
    padding: 0 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    @include on-tablet {
      max-width: 744px;
      padding: 0 72px;
    }

    @include on-desktop {
      max-width: 1260px;
      padding: 0 120px;
    }
  }

  &__title {
    font-family: Poppins, sans-serif;
    font-size: 36px;
    font-weight: 700;
    line-height: 36px;
    letter-spacing: -1.5;
    margin-bottom: 120px;

    @include on-tablet {
      font-size: 64px;
      font-weight: 700;
      line-height: 64px;
      letter-spacing: -1.5px;
      margin-bottom: 130px;
    }
  }
}
