.contact-us {
  @include on-tablet {
    padding-bottom: 102px;
  }

  @include on-desktop {
    padding-bottom: 164px;
  }

  &__title {
    @include section-title-mobile;

    margin-bottom: 48px;
  }

  &__container {
    @include on-tablet {
      display: grid;
      grid-template-columns: repeat(6, 1fr);
      grid-column-gap: 24px;
    }

    @include on-desktop {
      grid-template-columns: repeat(12, 1fr);
    }
  }
}
